
.tags-container[data-v-3a3e9e60]{
  padding: 3px;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 4px;
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, .1);
}
.tags[data-v-3a3e9e60]{
  float: left;
  padding: .5em .8em;
  padding-right: 2em;
  margin:3px;
  font-size: 12px;
  font-weight: 700;
  line-height: 1;
  color: #fff;
  background-color: #5bc0de;
  text-align: center;
  white-space: nowrap;
  border-radius: .4em;
}
.del[data-v-3a3e9e60]{
  position: absolute;
  font-size: 16px;
  margin-left: .5em;
  margin-top: -1px;
  cursor: pointer;
}
.tags-input[data-v-3a3e9e60]{
  display: block;
  max-width: 100%;
  height: 24px;
  margin:3px;
  font-size: 14px;
  line-height: 1.5;
  color: #555;
  border:0;
  outline: 0;
}



