
.v-spinner[data-v-e0cdbf32] {
  position: absolute;
  left:50%;
  top:40%;
  -webkit-transform:translateX(-50%);
          transform:translateX(-50%);
}
.v-spinner > div[data-v-e0cdbf32] {
  float: left;
  width: 18px;
  height: 18px;
  margin: 0 3px;
  background-color: #789;
  border-radius: 100%;
  -webkit-animation: sp-bounce 1.4s infinite ease-in-out;
          animation: sp-bounce 1.4s infinite ease-in-out;
}
.v-spinner > div[data-v-e0cdbf32]:nth-child(1) {
  -webkit-animation-delay: -0.32s;
          animation-delay: -0.32s;
}
.v-spinner > div[data-v-e0cdbf32]:nth-child(2) {
  -webkit-animation-delay: -0.16s;
          animation-delay: -0.16s;
}
@-webkit-keyframes sp-bounce {
0%, 80%, 100% { 
    -webkit-transform: scale(0);
    transform: scale(0);
}
40% { 
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
}
}
@keyframes sp-bounce {
0%, 80%, 100% { 
    -webkit-transform: scale(0);
    transform: scale(0);
}
40% { 
    -webkit-transform: scale(1.0);
    transform: scale(1.0);
}
}




